.error {
  position: relative;
  .text {
    position: relative;
    p {
      font-size: 1.2rem;
      font-weight: 600;
    }
    h1 {
      font-size: 5rem;
    }

    .rocket {
      //   transition: transform 300ms ease;
      position: absolute;
      animation: bounce 4s ease, sideways-bounce 4s ease infinite;
    }
  }
  .link {
    position: relative;
    text-decoration: none;
    color: #fff;
    top: 1.5rem;
    font-size: 1.25rem;
  }
}

@keyframes bounce {
  0% {
    transform: translate(0, 0);
  }
  20% {
    transform: translate(-5px, 5px);
  }
  40% {
    transform: translate(8px, -8px);
  }
  80% {
    transform: translate(-5px, 5px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}

@keyframes sideways-bounce {
  0% {
    transform: translate(0, 0);
  }
  20% {
    transform: translate(2px, 3px);
  }
  40% {
    transform: translate(-3px, -2px);
  }
  80% {
    transform: translate(2px, 3px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}

@media screen and (max-width: 600px) {
  .error {
    .text {
      position: relative;
      p {
        font-size: 1.2rem;
      }
      h1 {
        font-size: 2.7rem;
      }
    }
    .link {
      font-size: 1.2rem;
    }
  }
}
